.FormProducto {
  .images-productos {
    display: flex;
    flex-wrap: wrap;

    &__item {
      margin-left: 1.7rem;
      margin-bottom: 1rem;
      position: relative;
      width: 120px;
      &:first-child {
        margin-left: 0;
      }
      &__actions {
        position: absolute;
        top: -5px;
        right: -10px;
      }
    }
  }
}